.button {
    padding: 5px 24px;
    margin-right: 20px;
    background-color: #0066cc;
    color: white;
    border: 1px solid #0066CC;
    border-radius: 5px;
    cursor: pointer;
}

.disabled {
    opacity:40% ;
}

.secondary {
    border: 1px solid #0066CC;
    background-color: white;
    color: #0066CC;
}

.default {
    background-color: blue;
}

.red {
    background-color: red;
}

.default:hover {
    border: 1px solid black;
}

.red:hover {
    border: 1px solid rgb(76, 0, 0);
}

.secondary:hover {
    border: 1px solid #003585;
    background-color: #91aecc;
    color: white;
}


.default:active {
    border: 1px solid rgba(0, 94, 555, 0.383);
}

.secondary:active {
    border: 1px solid rgba(0, 94, 555, 0.383);
}

.red:active {
    border: 1px solid rgba(0, 94, 555, 0.383);
}

.button:disabled {
}
