.radio {
    visibility: hidden;
    margin-right: 15px;
}

.radio::before {
    visibility: visible;
    content: url('./radiobox-blank.svg');
}


.radio:checked::before {
    visibility: visible;
    content: url("./Vector.svg");
}

.label {
    display: flex;
    align-items: center;
    text-align: center;
    margin-top: 10px;
}

.options {
    display: flex;
    flex-direction: column;

}
